.team {
    text-align: center;
}

.team h2 {
    font-family: $font-family-Gilroy-Bold;
    font-size: 38px;
    color: #000;

    margin-top: 40px;
    margin-bottom: 80px;
}

.people {
    margin: 0;
    padding: 0;

    text-decoration: none;

    display: flex;
    justify-content: space-between;

    margin-bottom: 50px;
}

.person {
    width: 300px;

    list-style: none;
}

.person img {
    display: block;
    margin: 0 auto;
    margin-bottom: 20px;
}

.person b {
    font-family: $font-family-Gilroy-Bold;
    font-size: 18px;

    display: block;
    margin-bottom: 10px;
}

.person span {
    font-family: $font-family-Gilroy-Medium;
    font-size: 16px;

    display: block;
    margin-bottom: 20px;
}

.person p {
    font-family: $font-family-Gilroy-Medium;
    font-size: 14px;
}

.client {
    margin: 0 auto;
    display: block;
}

@media (max-width: 575.98px) {
    .person {
        margin-bottom: 30px;
    }
}