.mission {
    text-align: center;

    padding: 1px;
}

.mission h2 {
    font-family: $font-family-Gilroy-Bold;
    font-size: 38px;
    color: #000;

    margin-top: 40px;
    margin-bottom: 80px;
}

.features {
    margin: 0;
    padding: 0;

    text-decoration: none;

    display: flex;
    justify-content: space-between;

    margin-bottom: 50px;
}

.feature {
    text-align: left;

    position: relative;
    width: 300px;

    list-style: none;
}

.feature::after {
    content: '';

    position: absolute;

    top: -75px;
    right: 0;

    font-size: 288px;
    line-height: 1;

    color: rgba(0, 0, 0, 0.1);
}

.feature b {
    font-family: $font-family-Gilroy-Medium;
    font-size: 16px;
    color: #000;

    display: block;
    margin-bottom: 10px;
}

.feature:nth-child(1)::after {
    content: '1';
}

.feature:nth-child(2)::after {
    content: '2';
}

.feature:nth-child(3)::after {
    content: '3';
}

.feature p {
    font-family: $font-family-Gilroy-Regular;
    font-size: 14px;
    color: #525252;
}

@media (max-width: 991.98px) {
    .feature {
        margin-bottom: 100px;
    }
}