img {
  max-width: 100%; }

a {
  text-decoration: none; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0; }

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.wrapper--promo {
  background-color: #525252;
  background-image: url("../img/main-bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  margin-top: -131px;
  padding-top: 1px;
  min-height: 1024px; }

.wrapper--mission {
  background-color: #ebebeb; }

.wrapper--work {
  background-color: #ebebeb; }

.wrapper--footer {
  background-color: #262626; }

.promo {
  margin-top: 200px;
  text-align: center; }

.promo b {
  font-family: "Gilroy-Medium", "Verdana", sans-serif;
  font-size: 38px;
  color: #fff; }

.promo h1 {
  font-family: "Gilroy-Bold", "Verdana", sans-serif;
  font-size: 48px;
  color: #ebebeb; }

.presentation {
  width: 80%;
  height: 500px; }

@media (min-width: 320px) {
  .promo h1 {
    font-size: 40px; }
  .promo b {
    font-size: 30px; } }

@media (max-width: 575.98px) {
  .presentation {
    height: 300px; } }

@media (min-width: 992px) {
  .promo h1 {
    font-size: 48px; }
  .promo b {
    font-size: 38px; } }

.nav-link {
  font-family: "Gilroy-Medium", "Verdana", sans-serif;
  font-size: 16px;
  color: #fff; }

.navbar-collapse {
  justify-content: flex-end; }

.logo {
  width: 250px;
  height: auto; }

.mission {
  text-align: center;
  padding: 1px; }

.mission h2 {
  font-family: "Gilroy-Bold", "Verdana", sans-serif;
  font-size: 38px;
  color: #000;
  margin-top: 40px;
  margin-bottom: 80px; }

.features {
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px; }

.feature {
  text-align: left;
  position: relative;
  width: 300px;
  list-style: none; }

.feature::after {
  content: '';
  position: absolute;
  top: -75px;
  right: 0;
  font-size: 288px;
  line-height: 1;
  color: rgba(0, 0, 0, 0.1); }

.feature b {
  font-family: "Gilroy-Medium", "Verdana", sans-serif;
  font-size: 16px;
  color: #000;
  display: block;
  margin-bottom: 10px; }

.feature:nth-child(1)::after {
  content: '1'; }

.feature:nth-child(2)::after {
  content: '2'; }

.feature:nth-child(3)::after {
  content: '3'; }

.feature p {
  font-family: "Gilroy-Regular", "Verdana", sans-serif;
  font-size: 14px;
  color: #525252; }

@media (max-width: 991.98px) {
  .feature {
    margin-bottom: 100px; } }

.team {
  text-align: center; }

.team h2 {
  font-family: "Gilroy-Bold", "Verdana", sans-serif;
  font-size: 38px;
  color: #000;
  margin-top: 40px;
  margin-bottom: 80px; }

.people {
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px; }

.person {
  width: 300px;
  list-style: none; }

.person img {
  display: block;
  margin: 0 auto;
  margin-bottom: 20px; }

.person b {
  font-family: "Gilroy-Bold", "Verdana", sans-serif;
  font-size: 18px;
  display: block;
  margin-bottom: 10px; }

.person span {
  font-family: "Gilroy-Medium", "Verdana", sans-serif;
  font-size: 16px;
  display: block;
  margin-bottom: 20px; }

.person p {
  font-family: "Gilroy-Medium", "Verdana", sans-serif;
  font-size: 14px; }

.client {
  margin: 0 auto;
  display: block; }

@media (max-width: 575.98px) {
  .person {
    margin-bottom: 30px; } }

.work {
  text-align: center;
  padding-top: 1px;
  padding-bottom: 40px; }

.work h2 {
  font-family: "Gilroy-Bold", "Verdana", sans-serif;
  font-size: 38px;
  color: #000;
  margin-top: 40px;
  margin-bottom: 40px; }

.notes {
  text-align: center;
  padding-top: 1px;
  padding-bottom: 40px; }

.notes h2 {
  font-family: "Gilroy-Bold", "Verdana", sans-serif;
  font-size: 38px;
  color: #000;
  margin-top: 40px;
  margin-bottom: 40px; }

.footer {
  color: #000;
  display: flex;
  justify-content: space-between;
  min-height: 100px;
  padding-top: 35px; }

.footer p {
  font-family: "Gilroy-Medium", "Verdana", sans-serif;
  font-size: 24px;
  color: #ffffff; }

.footer span {
  font-family: "Gilroy-Medium", "Verdana", sans-serif;
  font-size: 24px;
  color: #ffffff; }

.social {
  display: flex;
  justify-content: space-between;
  list-style: none;
  width: 120px; }

.social__item, .social__item a {
  display: block;
  list-style: none;
  width: 30px;
  height: 30px;
  position: relative; }

.social__item::before {
  content: '';
  position: absolute;
  width: 30px;
  height: 30px;
  top: 0;
  left: 0;
  background-size: 30px 30px;
  background-position: center;
  background-repeat: no-repeat; }

.social__item:nth-child(1)::before {
  background-image: url("../img/github.png"); }

.social__item:nth-child(2)::before {
  background-image: url("../img/youtube.png"); }
