.wrapper {
    &--promo {
        background-color: #525252;
        background-image: url("../img/main-bg.png");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;

        margin-top: -131px;
        padding-top: 1px;

        min-height: 1024px;
    }

    &--mission {
        background-color: $bg-gray;
    }

    &--work {
        background-color: $bg-gray;
    }

    &--footer {
        background-color: #262626;
    }
}