.promo {
    margin-top: 200px;
    text-align: center;
}

.promo b {
    font-family: $font-family-Gilroy-Medium;
    font-size: 38px;
    color: #fff;
}

.promo h1 {
    font-family: $font-family-Gilroy-Bold;
    font-size: 48px;
    color: #ebebeb;
}

.presentation {
    width: 80%;
    height: 500px;
}


@media (min-width: 320px) {
    .promo h1 {
        font-size: 40px;
    }

    .promo b {
        font-size: 30px;
    }
}

@media (max-width: 575.98px) {
    .presentation {
        height: 300px;
    }
}

@media (min-width: 992px) {
    .promo h1 {
        font-size: 48px;
    }

    .promo b {
        font-size: 38px;
    }
}

