.footer {
    color: #000;

    display: flex;
    justify-content: space-between;

    min-height: 100px;

    padding-top: 35px;
}

.footer p {
    font-family: $font-family-Gilroy-Medium;
    font-size: 24px;
    color: #ffffff;
}

.footer span {
    font-family: $font-family-Gilroy-Medium;
    font-size: 24px;
    color: #ffffff;
}

.social {
    display: flex;
    justify-content: space-between;

    list-style: none;

    width: 120px;
}

.social__item, .social__item a {
    display: block;

    list-style: none;

    width: 30px;
    height: 30px;

    position: relative;
}

.social__item::before {
    content: '';

    position: absolute;

    width: 30px;
    height: 30px;

    top: 0;
    left: 0;

    background-size: 30px 30px;
    background-position: center;
    background-repeat: no-repeat;
}

.social__item:nth-child(1)::before {
    background-image: url("../img/github.png");
}

.social__item:nth-child(2)::before {
    background-image: url("../img/youtube.png");
}
